import React from 'react'
import styled from 'styled-components'
import FolioCard from './foliocard'

export default styled(({ className, title = 'Folios', folios = [] }) => {
  return (
    <div className={className}>

      <div> 
        {folios.map(folio => (
        <FolioCard 
          key = {folio.pic} 
          
          name = {folio.title} 
          text = {folio.text} 
          pic = {folio.pic}
          picDesc = {folio.picDesc}
        />
      ))} 
      </div>
      

    </div>
  )
})`
  width: 100%;
  padding-bottom: 0px;
`
