import React from 'react'
import styled from 'styled-components'

const FolioCard = ({ className, name, text, pic, picDesc }) => {
  return (
    
    <div className={className} >
      

        <h3>{name}</h3>
          <div id={`${name}`} className={'content'} >
          <img src={pic} alt={picDesc} className={'avatar'}/>
        <p className={'excerpt'}>{text}</p>
      </div>
    </div>
    

  )
}

const FolioVideo = ({ className, name, text, pic, picDesc }) => {
  return (
    
    <div className={className} >
      

        <h3>{name}</h3>
          <div id={`${name}`} className={'content'} >
          <img src={pic} alt={picDesc} className={'avatar'}/>



        <p className={'excerpt'}>{text}</p>
      </div>
    </div>
    

  )
}

